<template>
    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeIn"
        leave-active-class="animate__fadeOut"
    >
        <li>
            <label>
                <input type="checkbox" :checked="todo.done" @change="handleCheck(todo.id)"/>
                <span v-show="!todo.isEdited">{{todo.title}}</span>
                <input 
                    type="text" 
                    v-show="todo.isEdited" 
                    :value="todo.title" 
                    @blur="handleBlur(todo,$event)"
                    ref="inputTitle"
                >
            </label>
        
        <button class="btn btn-danger" @click="handleDelete(todo.id)">删除</button>
        <button v-show="!todo.isEdited" 
                class="btn btn-edit" 
                @click="handleEdit(todo)"
        >编辑</button>
    </li>
    </transition>
</template>

<script >
import "animate.css";
export default {
    name: "MyItem",
    //声明接收todo对象
    props: ['todo'],
    methods: {
        handleCheck(id) {
            // 通知App组件将对应的todo对象的done值取反
            this.$bus.$emit("checkTodo", id)
        },
        handleDelete(id) {
            if (confirm("确定删除吗？")) {
                this.$bus.$emit("deleteTodo", id)
                // this.deleteTodo(id)
            }
            
        },
        //修改框
        handleEdit(todo) {
            if (todo.hasOwnProperty('isEdited') ) {
                todo.isEdited = true;
            } else {
                this.$set(todo,'isEdited',true)
            }
            // //0.2秒后，获取焦点
            // setTimeout(() => {
            //     this.$refs.inputTitle.focus()
            // },200)
            this.$nextTick(function () {
                this.$refs.inputTitle.focus()
            })
            
        },
        //失去焦点时变成msg
        handleBlur(todo,e) {
            todo.isEdited = false
            if(!e.target.value.trim()) return alert('输入不能为空')
            this.$bus.$emit('updateTodo',todo.id ,e.target.value)
        }
    },

}
</script>

<style scoped>
li {
        list-style: none;
        height: 36px;
        line-height: 36px;
        padding: 0 5px;
        border-bottom: 1px solid #ddd;
    }

    li label {
        float: left;
        cursor: pointer;
    }

    li label li input {
        vertical-align: middle;
        margin-right: 6px;
        position: relative;
        top: -1px;
    }

    li button {
        float: right;
        display: none;
        margin-top: 3px;
        /* background-color: red; */
        color: white;
        border-color: #ddd;
    }

    li:before {
        content: initial;
    }

    li:last-child {
        border-bottom: none;
    }

    li:hover {
        background-color: #eee;
    }

    li:hover button{
        display: block;
    }

    /*   /*自己写的
    .todo-enter-active{
		animation: xujing 0.2s linear;
	}

	.todo-leave-active{
		animation: xujing 0.2s linear reverse;
	}

	@keyframes xujing {
		from{
			transform: translateX(100%);
		}
		to{
			transform: translateX(0px);
		}
	} */
</style>